.reservation {
    display: block;

    width: 99%;
    /* width: 70em; */
    height: fit-content;

    margin: 1px;


    border: 1px solid;
    border-radius: 3px;

    text-align: left;
}

.reservation:hover {
    cursor: pointer;
}

.reservation span {
    margin-left: 0.5em;
    display: inline-flex;
    overflow: hidden;
}


.timeVal {
    width: 1.5em;
}
.tarif{
    width: 5em;
}

.instructorSign{
    display: inline;
    width: 1em;
    border-radius: 5px;
    text-align: center;
    color: rgb(96, 141, 92);
    font-weight: bold;
}
.instructorSign.needInstructor{
    width: 1em;
    /* border: 1px solid black */
    color: rgb(255, 3, 3);
    background-color: white;
    font-size: bold;
}


.flyer{
    width: 15em;
}
.notes{
    width: 15em;
}
.id{
    width: 7em;
}
.payment{
    width: 7em;
}

.reservation-detailed {
    overflow: hidden;
    padding-left: 3em;
    text-align: left;
    font-style: italic;
}


.subFlyerTbody th,
.subFlyerTbody td {
    min-width: 2em;
    padding: 0 0.5em 0 0.5em;
    text-align: center;
}

.subFlyerTbody th {
    font-weight: bold;
}