.timeSlot {
    display: flex;

    height: fit-content;

    margin: 0.5em;

    border: 2px solid;
    border-radius: 3px;

    background-color: #defaff;
}

.slotTimeHeader {
    position: sticky;
    left: 0;
    padding: 0.5em 0 0.5em 0;

    width: 5em;
    /* margin: 1px; */

    background-color: rgb(155, 198, 255);
    border-radius: 3px;
}

.reservationsContainer{
    width: 99%;
}

.sLotBottomLine {
    /* padding-left: 2em; */
    height: 1.5em;
    text-align: left;
}

.sLotBottomLine .controlsTmp {
    margin-left: 1em;
    font-style: italic;
    color: rgb(141, 141, 141);
}