.daysContainer {
    position: relative;
    display: block;
    overflow: scroll;
    overflow-x: hidden;

    /* width: 50em; */
    /* width: 99%; */
    height: 90vh;

    margin: 0.5em;

    border: 2px solid;
    border-radius: 3px;
}

.mainHeaderSlot {
    display: flex;
    height: 2em;
    /* border: 2px solid;
    border-radius: 3px; */
    background-color: white;
}

.mainHeader-timeCol {
    position: sticky;
    top: 0;
    left: 0;

    width: 5em;

    background-color: rgb(71, 108, 255);
    color: white;
    font-weight: bold;
}

/*
.mainHeader-restCols {
    width: 71em;
    height: 2em;

    text-align: left;
    background-color: rgb(71, 108, 255);
}
*/

.mainHeader-restCols span {
    margin-left: 0.5em;
    display: inline-flex;
    overflow: hidden;
}

.dayBlockDiv.mainHeader {
    position: sticky;
    top: 0;
    z-index: 10;

    display: inline-flex;

    width: 95%;

    padding: 0.5em 0 0.5em 0;

    text-align: center;
    font-weight: bold;

    background-color: rgb(71, 108, 255);
    color: white;
}

.dayBlockDiv.mainHeader button {
    height: 1.5em;
    margin-left: 1em;

    background-color: rgb(71, 108, 255);
    border-radius: 5px;
    border: 2px solid white;
    color: white;
}

.dayBlockDiv.mainHeader button:active {
    background-color: white;
    color: rgb(71, 108, 255);
}

